@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://cdn.jsdelivr.net/gh/tonsky/FiraCode@1.207/distr/fira_code.css);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}html,body,#root{height:100%;width:100%;overflow:hidden;margin:0;padding:0}#root{display:flex;flex-direction:column;align-items:center;background-color:#011627}#root .app-header{color:#efffe9;font-family:"Lato",sans-serif;font-size:2rem;text-transform:uppercase;margin:2rem 1rem;text-align:center}#root .app-content{margin:1rem;overflow-y:scroll;background-color:#efffe9;max-width:35rem}#root .client-list-header{color:#011627;text-transform:uppercase;font-size:1.5rem;font-family:"Fira Code",monospace;font-weight:500;margin:1.5rem 2rem}#root .client-list--loading{font-family:"Lato",sans-serif;font-size:1.5rem;color:#cb0;text-transform:uppercase;margin:2rem;text-align:center}#root .client-list--error{max-width:20rem;font-family:"Lato",sans-serif;font-size:1.5rem;color:red;text-transform:uppercase;margin:2rem}#root .client-list--error>*{text-transform:none;font-size:1rem}#root .client-list-item{display:flex;flex-wrap:wrap;justify-content:space-between;margin:0 2rem 0.5rem;font-family:"Lato",sans-serif;color:#011627}#root .client-list-item:not(:first-child){border-top:1px solid #011627;padding-top:1rem}#root .client-list-item-name{flex-basis:50%;min-width:50%}#root .client-list-item-state{flex-basis:50%;min-width:50%;text-align:right;font-weight:600}#root .client-list-item-state--connected{color:#0a0}#root .client-list-item-state--disconnected{color:red}#root .client-list-item-state--loading{color:#cb0}#root .client-list-item-toggle{margin-left:auto;margin-top:0.5rem;padding:0.5rem 1rem;border-radius:0.5rem;border:0;background-color:#011627;color:#efffe9;text-transform:uppercase;font-weight:600;cursor:pointer}#root .client-list-item-devices{flex-basis:100%}#root .client-list-item-devices li:not(:first-child) dl{border-top:1px solid rgba(1,22,39,0.2);padding-top:0.5rem}#root .client-list-item-devices-list{display:flex;max-width:20rem;flex-wrap:wrap;margin-bottom:0.5rem}#root .client-list-item-devices-list dt,#root .client-list-item-devices-list dd{overflow-wrap:break-word;margin-top:0.5rem}#root .client-list-item-devices-list dt{flex-basis:30%;max-width:30%}#root .client-list-item-devices-list dd{flex-basis:70%;max-width:70%;text-align:right}#root .ip-and-dns-info-header{color:#011627;text-transform:uppercase;font-size:1.5rem;font-family:"Fira Code",monospace;font-weight:500;margin:1.5rem 2rem}#root .ip-and-dns-info--loading,#root .ip-and-dns-info--error{font-family:"Lato",sans-serif;font-size:1.1rem;text-transform:uppercase;margin-left:2rem;margin-top:0.5rem}#root .ip-and-dns-info--loading{color:#cb0}#root .ip-and-dns-info--error{color:red}#root .ip-and-dns-info-name{font-family:"Lato",sans-serif;color:#011627;margin-left:2rem;margin-top:0.5rem}#root .ip-and-dns-info-list{margin-left:2.5rem;margin-right:2rem;display:flex;flex-wrap:wrap;max-width:20rem;font-family:"Lato",sans-serif;color:#011627}#root .ip-and-dns-info-list dt,#root .ip-and-dns-info-list dd{flex-basis:50%;margin-top:0.5rem}#root .ip-and-dns-info-list dd{text-align:right}

